
.app {
  text-align: center;
  min-height: 100vh;
  color: #404040;
  overflow-y: hidden;
  overflow-x: hidden;
  max-width: 100vw;
}

.app-header {
  margin-bottom: .5em;
  margin-top: 3em;
  font-weight: bold;
}

.app-phrase {
  margin-right: auto;
  margin-left: auto;
  line-height: 2em;
  max-width: 50%;
  margin-bottom: 8em;
}

.app-image {
  margin: 0 auto;
}


#container {
  max-width: 100vw;
}

.about {
  max-width: 100vw;
}

.about-info,
.contact,
.projects {
  margin-left: auto;
  margin-right: auto;
  max-width: 75%;
  margin-top: 10em;
}

.about-header {
  margin-bottom: .5em;
  font-weight: bold;
}

.about-description {
  line-height: 2em;
}

#about-skills {
  margin-top: 10em;
}

.card {
  color: #404040;
  margin: 3em;
  padding: 2.5em;
  min-width: 50%;
}

.card-title {
  font-weight: bold;
}

.btn {
  background-image: linear-gradient(90deg, #FF713B, #FFA51D);
  border: none;
  border-radius: 3px;
  padding: 1rem;
  color: white;
  transform: scale(1);
  transition: transform .5s;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.btn:hover,
.btn:focus,a:focus {
  outline: none;
}

.btn:hover,
.btn:focus {
  transform: scale(1.05);
}

.contact {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5em;
  max-width: 50%;
}

.contact-list {
  display: flex;
  list-style: none;
  align-items: center;
  justify-content: space-evenly;
  padding: 0;
  margin: 0;
}

#contact-socials {
  padding: 1rem;
  margin-left: auto;
  margin-right: auto;
  background-image: linear-gradient(90deg, #FF713B, #FFA51D);
  border-radius: 3px;
}

.contact-image {
  max-width: 32px;
  margin-left: auto;
  margin-right: auto;
}

.todo {
  text-align: center;
  max-width: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 4em;
  background-color: white;
  padding: 1em;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2), 0 2.5rem 5rem 0 rgba(0, 0, 0, .1);
  color: #292929;
}

.todo-header {
  font-weight: 400;
  font-size: 1.5rem;
}

.todo-input {
  width: 100%;
  height: 40px;
  border-radius: 20px;
  text-align: center;
  border-style: solid;
  border-color: #d8d8d8;
  font-size: 1em;
  font-weight: light;
}

.todo-add,
.todo-clear {
  margin-top: 1em;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  border-style: solid;
  border-color: #d8d8d8;
  background-color: white;
}

.todo-input:focus {
  outline: none;
}

.todo-add:hover,
.todo-clear:hover {
  box-shadow: 1px 1px 1px #5f5f5f;
}

.todo-add:active,
.todo-clear:active {
  box-shadow: inset 1px 1px 1px #5f5f5f;
}

.todo-task {
  margin-top: 1em;
  display: flex;
  justify-content: center;
}

.todo-task label {
  display: flex;
  justify-content: space-between;
}

.todo-task input {
  position: relative;
  bottom: 1px;
  margin-right: 10px;
}

.todo-task-checkbox:checked {
  box-shadow: 1px 1px 1px #5f5f5f;
  accent-color: #d1d1d1;
}

.todo-clear {
  margin-top: 3em;
  width: 128px;
  height: 32px;
  border-radius: 16px;
}

.todo-task-remove {
  width: 20px;
  height: 20px;
  position: relative;
  bottom: 1px;
  margin-left: 10px;
}

.tictactoe {
  color: #292929;
  font-weight: 300;
}

.tictactoe > h1 {
  margin-top: 1em;
  text-align: center;
  font-weight: 400;
}

.tictactoe-container{
  margin-top: 3em;
  display: grid;
  grid-template-columns: repeat(3, 0fr);
  gap: 10px;
  grid-auto-rows: minmax(100px, auto);
  justify-content: center;
}

.tictactoe-square {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 6em;
  height: 6em;
  border: 1px solid #333333;
  user-select: none;
  box-shadow: 1px 1px 1px #5f5f5f;
}

.tictactoe-square:hover {
  cursor: pointer;
}

.tictactoe-square span {
  font-size: 64px;
  font-weight: 100;
}

.tictactoe-winner {
  margin-top: 1em;
  text-align: center;
}

.tictactoe-reset {
  display: flex;
  margin-top: 1em;
  margin-left: auto;
  margin-right: auto;
  width: 128px;
  height: 64px;
  border-radius: 32px;
  border-style: solid;
  border-color: #d8d8d8;
  background-color: white;
}

.tictactoe-reset span {
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  font-size: large;
}

.tictactoe-reset:hover {
  box-shadow: 1px 1px 1px #5f5f5f;
}

.tictactoe-reset:active {
  box-shadow: inset 1px 1px 1px #5f5f5f;
}

div.is-disabled
{
  pointer-events: none;
}

.weatherapp {
  color: #292929;
  user-select: none;
}

.weatherapp-header {
  font-weight: 400;
}

.weatherapp-topbanner {
  display: flex;
  justify-content: center;
  margin: 3em 0;
}

.weatherapp-message {
  margin-top: 1em;
  display: flex;
  justify-content: center;
}

.weatherapp-search {
  display: block;
  margin-top: 1em;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
}

.weatherapp-search input {
  padding: .25rem .5rem;
}

.weatherapp-search input:focus {
  outline: none;
}

.weatherapp-search-button {
  background-image: linear-gradient(90deg, #FF713B, #FFA51D);
  border: none;
  border-radius: 3px;
  padding: .25rem;
  transform: scale(1);
  transition: transform .5s;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
}

.weatherapp-search-button:hover {
  transform: scale(1.2) translateX(9%);
}

.weatherapp-container form {
  margin-top: 2em;
}

.weatherapp-dynamic {
  margin: 0 auto;
}

.weatherapp-cities {
  list-style: none;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  justify-content: center;
  gap: 1em;
  padding: 0;
}

.weatherapp-cities > li {
  width: fit-content;
  margin: 0 auto;
}

.weatherapp-city {
  width: 256px;
  background-image: linear-gradient(90deg, #FF713B, #FFA51D);
  padding: 1em;
  border: 1px solid #ffffff;
  border-radius: 2em;
  box-shadow: 1px 1px 1px #5f5f5f;
  color: white;
  
}

.weatherapp-city > h4, .weatherapp-city > h5, .weatherapp-city > h6 {
  font-weight: bold;
  font-size: 1em;
  margin: 1rem;
}

.weatherapp-city-delete {
  margin: 0;
  text-align: right;
}
.weatherapp-city-delete:hover {
  cursor: pointer;
}

@media (min-width: 600px) {
  .weatherapp-cities { grid-template-columns: repeat(2, 0fr); }
}

@media (min-width: 900px) {
  .weatherapp-cities { grid-template-columns: repeat(3, 0fr); }
}